<template>
    <div class="asset-information">
        <div class="asset-c">
            <!-- 资产列表 -->
            <div class="asset-detail" v-if="shoWextract == 0">
                <div class="asset-title">
                    <span class="desc"></span>
                    <span class="name">{{$t('center.assetInformation[0]')}}</span>
                </div>
                <div class="select-warp">
                    <div class="select-c">
                        <span class="label">{{$t('center.assetInformation[1]')}}</span>
                        <template>
                            <el-select v-model="option1Value" :placeholder="$t('center.assetInformation[2]')" @change="option1Change" class="select-el">
                                <el-option
                                v-for="item in option1"
                                :key="item.pid"
                                :label="item.pname"
                                :value="item.code">
                                </el-option>
                            </el-select>
                        </template>
                        <el-input v-model="changeNum1" @input="num1change" class="select-input" :placeholder="$t('center.assetInformation[3]')"></el-input>
                        <img src="../../../assets/img/center/change.png" alt="" class="change-img">
                        <template>
                            <el-select v-model="option2Value" :placeholder="$t('center.assetInformation[2]')" class="select-el">
                                <el-option
                                v-for="item in option2"
                                :key="item.id"
                                :label="item.ex_name"
                                :value="item.ex_coin">
                                </el-option>
                            </el-select>
                        </template>
                        <el-input v-model="changeNum2" disabled class="select-input" :placeholder="$t('center.assetInformation[3]')"></el-input>
                        <button class="submit-btn" @click="submitChange">{{$t('center.assetInformation[4]')}}</button>
                    </div>
                </div>
                <template>
                    <el-table
                    :data="tableData"
                    v-loading="tableLoading"
                    style="width: 100%">
                    <el-table-column
                        prop="pname"
                        :label="$t('center.assetInformation[5]')"
                        width="180">
                    </el-table-column>
                    <el-table-column
                        prop="usable"
                        :label="$t('center.assetInformation[6]')"
                        width="180">
                    </el-table-column>
                    <el-table-column
                        prop="frost"
                        :label="$t('center.assetInformation[7]')">
                    </el-table-column>
                    <el-table-column
                        prop="address"
                        :label="$t('center.assetInformation[8]')"
                        align="right">
                        <template slot-scope="scope">
                            <span class="handel-detail" @click="topUp(scope.row.pid,scope.row.pname)">{{$t('center.assetInformation[9]')}}</span>
                            <span v-if="Number(scope.row.usable) > 0" class="handel-detail" @click="wextract(scope.row.pid,scope.row.pname)">{{$t('center.assetInformation[10]')}}</span>
                            <!-- <span class="handel-detail">划转</span> -->
                        </template>
                    </el-table-column>
                    </el-table>
                </template>
            </div>
            <!-- 提币 -->
            <div class="asset-extract" v-if="shoWextract == 1">
                <div class="asset-title">
                    <span class="desc"></span>
                    <span class="name">{{$t('center.assetInformation[10]')}}</span>
                </div>
                <p class="input-label">{{$t('center.assetInformation[11]')}}</p>
                <div class="input-warp">
                    <el-input v-model="valueExtract" placeholder="" class="extract-input"></el-input>
                    <template>
                        <el-select v-model="valueExtract" :placeholder="$t('center.assetInformation[12]')" class="select-el">
                            <el-option
                            v-for="item in addressList"
                            :key="item.qiaobao_url"
                            :label="item.qiaobao_url"
                            :value="item.qiaobao_url">
                            </el-option>
                        </el-select>
                    </template>
                </div>
                <p class="input-label">{{$t('center.assetInformation[13]')}}</p>
                 <div class="input-warp">
                    <el-input v-model="tbNumber" :placeholder="$t('center.assetInformation[14]')" class="extract-input"></el-input>
                    <span class="max-extract">{{$t('center.assetInformation[15]')}}{{tbLimit.tb_max + pname}}</span>
                </div>
                <p class="input-label">{{$t('center.assetInformation[16]')}}</p>
                 <div class="input-warp">
                    <el-input v-model="payPsw" :placeholder="$t('center.assetInformation[17]')" type="password" class="extract-input"></el-input>
                </div>
                <p class="input-label">{{isTelLogin ? $t('center.assetInformation[18]') : $t('center.assetInformation[19]')}}{{$t('center.assetInformation[20]')}}</p>
                 <div class="input-warp">
                    <el-input v-model="telCode" :placeholder="$t('center.assetInformation[21]')" class="extract-input"></el-input>
                    <el-button type="primary" class="" :loading="getCodeLoading" :disabled="!ableGetCode" @click="getCodeRequest">{{getCodeText}}</el-button>
                </div>
                <el-button class="extract-btn" @click="extract">{{$t('center.assetInformation[10]')}}</el-button>
                <p class="tips">{{$t('center.assetInformation[22]')}}</p>
                <p class="tips">{{$t('center.assetInformation[23]')}}</p>
            </div>
        </div>
        <el-dialog
        :title="$t('center.assetInformation[1]')"
        :visible.sync="dialogVisible"
        width="26%">
            <p class="change-detail">
                <span>{{changeNum1}}</span>
                <span>{{changeCoin1}}</span>
                {{$t('center.assetInformation[24]')}}
                <span>{{changeNum2}}</span>
                <span>{{changeCoin2}}</span>
            </p>
            <el-input v-model="moneyPwd" type="password" :placeholder="$t('center.assetInformation[25]')"></el-input>
            <div class="btn-group clr">
                <el-button type="primary" @click="exchange" class="dialog-btn">{{$t('center.assetInformation[26]')}}</el-button>
                <el-button @click="dialogVisible = false" class="dialog-btn">{{$t('center.assetInformation[27]')}}</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import {sendMsg,sendEmailMsg} from '@/api/public.js'
import {assetList,coinList,thisCoinList,exchange,addrList,getTbLimit,tiBi} from "@/api/user.js"
export default {
    name: 'assetInformation',
    data() {
        return {
            changeNum1:'',
            changeNum2:'',
            changeCoin1:'',
            changeCoin2:'',
            exRate:'',
            moneyPwd:'',
            shoWextract: 0,
            dialogVisible: false,
            tableLoading:false,
            option1:[],
            option2: [],
            option1Value: '',
            option2Value: '',
            valueExtract: '',
            tableData: [],
            addressList:[],
            pid:'',
            pname:'',
            tbLimit:{},
            payPsw:'',
            getCodeText: this.$t('center.assetInformation[28]'),
            getCodeLoading:false,
            ableGetCode:true,//验证码按钮能否点击
            countDownNumber:60,//倒计时秒数
            countDownTimer:null,//定时器
            telCode:'',
            tbNumber:'',
            sessionData:{},
            isTelLogin: this.$public.checkMobile(sessionStorage.getItem('mobile'))
        }
    },
    created () {
        this.sessionData = JSON.parse(sessionStorage.getItem('userInfo'))
        this.getTableData()
        this.getCoinList()
    },
    methods: {
        getCodeRequest(){//发送获取验证码请求接口
            this.getCodeLoading = true
            this.ableGetCode = false
            if(this.isTelLogin){//手机登录下发送短信验证码
                let _data = {
                    mobile: sessionStorage.getItem('mobile'),
                    mcode: this.sessionData.mcode,
                    type:5
                }
                sendMsg(_data).then((res)=>{
                    if(res.data.status == 200){
                    this.getCodeLoading = false
                    this.$public.msg(this.$t('center.assetInformation[29]'),'success',this)
                    this.countDown()
                    } else{
                    this.getCodeLoading = false
                    this.$public.msg(res.data.msg,'error',this)
                    this.ableGetCode = true
                    }
                },
                ()=>{
                    this.$public.msg(this.$t('center.assetInformation[30]'),'error',this)
                    this.getCodeLoading = false
                    this.ableGetCode = true
                })
            } else{//发送邮箱验证码
                let _data = {
                    email: sessionStorage.getItem('mobile')
                }
                sendEmailMsg(_data).then((res)=>{
                    if(res.data.status == 200){
                        this.getCodeLoading = false
                        this.$public.msg(this.$t('center.assetInformation[29]'),'success',this)
                        this.countDown()
                    } else{
                        this.getCodeLoading = false
                        this.$public.msg(res.data.msg,'error',this)
                        this.ableGetCode = true
                    }
                },
                ()=>{
                    this.$public.msg(this.$t('center.assetInformation[30]'),'error',this)
                    this.getCodeLoading = false
                    this.ableGetCode = true
                })
            }
            
        },
        countDown(){//验证码倒计时
            this.ableGetCode = false
            let countDownCopy = this.countDownNumber
            this.countDownTimer = setInterval(()=>{
                if(this.countDownTimer && countDownCopy<=0){
                    clearInterval(this.countDownTimer)
                    this.getCodeText = this.$t('center.assetInformation[31]')
                    this.ableGetCode = true
                    return
                }
                this.getCodeText = countDownCopy
                countDownCopy--;
            },1000)
        },
        getAddressList(){
            addrList({pid:this.pid}).then((res)=>{
                if(res.data.status == 200){
                    if(this.pname == 'BTC' || this.pname == 'USDT') {
                        this.addressList = res.data.data.btc;
                    } else if(this.pname == 'ETH' || this.pname == 'BF') {
                        this.addressList = res.data.data.eth;
                    } else {
                        this.addressList = res.data.data.eth;
                    }
                } else{
                    this.$public.msg(res.data.msg,'error',this)
                }
            })
        },
        getTbLimit(){
            getTbLimit({pid:this.pid}).then((res)=>{
                if(res.data.status == 200){
                    this.tbLimit = res.data.data
                } else{
                    this.$public.msg(res.data.msg,'error',this)
                }
            })
        },
        num1change(v){
            if(!this.$public.Ints(Number(v)) && v != ''){
                this.$public.msg(this.$t('center.assetInformation[32]'),'error',this)
                return
            }
            if(this.changeNum1 && this.exRate){
                this.changeNum2 = parseFloat(this.changeNum1)*this.exRate
            } else{
                this.changeNum2 = ''
            }
        },
        // 根据option1Value获取对应lable名称
        findLableByValue1(value){
            let result = ''
            this.option1.forEach((item)=>{
                if(item.code == value){
                    result = item.pname
                }
            })
            return result
        },
        // 根据option2Value获取对应lable名称
        findLableByValue2(value){
            let result = ''
            this.option2.forEach((item)=>{
                if(item.ex_coin == value){
                    result = item.ex_name
                }
            })
            return result
        },
        findOptionLableByValue1(value){
            let result = ''
            this.option1.forEach((item)=>{
                if(item.code == value){
                    result = item.pname
                }
            })
            return result
        },
        // 币种的兑换列表
        getCoinList(){
            coinList().then((res)=>{
                if(res.data.status == 200){
                    this.option1 = res.data.data
                } else{
                    this.$public.msg(res.data.msg,'error',this)
                }
            })
        },
        // 可以兑换币种的列表
        getThisCoinList(coin){

            thisCoinList({coin:coin}).then((res)=>{
                if(res.data.status == 200){
                    this.option2 = res.data.data
                    this.option2Value = res.data.data[0].ex_coin
                    this.exRate = parseFloat(res.data.data[0].ex_rate)
                } else{
                    this.$public.msg(res.data.msg,'error',this)
                }
            })
        },
        // 获取表格数据
        getTableData(){
            this.tableLoading = true
            assetList().then((res)=>{
                if(res.data.status == 200){
                    this.tableLoading = false
                    this.tableData = res.data.data.asset
                    this.tableData.forEach((item)=>{
                        // 统一保留四位小数
                        item.frost = this.$public.toIntercept(item.frost,4)
                        item.usable = this.$public.toIntercept(item.usable,4)
                    })
                } else{
                    this.tableLoading = false
                    this.$public.msg(res.data.msg,'error',this)
                }
            })
        },
        option1Change(v){
            this.changeNum1 = ''
            this.changeNum2 = ''
            this.getThisCoinList(v)
        },
        // 资金兑换确认
        exchange() {
            if(!this.moneyPwd){
                this.$public.msg(this.$t('center.assetInformation[33]'),'warning',this)
                return
            }
            this.$public.googleVerify().then((r)=> {
                if(r){//验证通过
                    this.exchangeRequest()
                } else{
                    this.$public.msg(this.$t('center.assetInformation[34]'),'error',this)
                }
            }).catch((r)=>{
                console.log('取消')
            })
            
        },
        
        exchangeRequest(){
            let _data = {
                coin: this.option1Value,
                ex_coin: this.option2Value,
                num: this.changeNum2,
                tpwd: this.$public.Cryptographiccapital(this.moneyPwd)
            }
            exchange(_data).then((res)=>{
                if(res.data.status == 200){
                    this.$public.msg(this.$t('center.assetInformation[35]'),'success',this)
                    this.getTableData()
                    this.dialogVisible = false
                } else{
                    this.$public.msg(res.data.msg,'error',this)
                }
            })
        },
        // 充币
        topUp(id,name) {
            this.$router.push({ name: 'topUp',params: {id: id,pname:name} })
        },
        // 提币
        wextract(id,name) {
            this.pid = id
            this.pname = name
            this.shoWextract = 1
            this.getAddressList()
            this.getTbLimit()
        },
        // 确认提币
        extract() {
            if(!this.valueExtract){//请选择或填写提币地址！
                this.$public.msg(this.$t('center.assetInformation[36]'),'warning',this)
                return
            }
            if(!this.$public.integer(this.tbNumber)){//提币数量输入错误！
                this.$public.msg(this.$t('center.assetInformation[37]'),'warning',this)
                return
            }
            if(!this.$public.pwd(this.payPsw)){//支付密码格式错误！
                this.$public.msg(this.$t('center.assetInformation[38]'),'warning',this)
                return
            }
            if(!this.$public.code(this.telCode)){//验证码码格式错误！
                this.$public.msg(this.$t('center.assetInformation[39]'),'warning',this)
                return
            }
            this.$public.googleVerify().then((r)=> {
                if(r){//验证通过
                    this.extractRequest()
                } else{
                    this.$public.msg(this.$t('center.assetInformation[40]'),'error',this)
                }
            }).catch((r)=>{
                console.log('取消')
            })
        },
        // 提币请求
        extractRequest(){
            let _data = {
                num: this.tbNumber,
                pid: this.pid,
                tpwd: this.$public.Cryptographiccapital(this.payPsw),
                code: this.telCode,
                account: sessionStorage.getItem('mobile'),
                address: this.valueExtract
            }
            tiBi(_data).then((res)=>{
                if(res.data.status == 200){
                    this.shoWextract = 0
                    this.getTableData()
                    this.getCoinList()
                    this.$public.msg(this.$t('center.assetInformation[41]'),'success',this)
                } else{
                    this.$public.msg(res.data.msg,'error',this)
                }
            })
        },
        // 提交兑换
        submitChange() {
            if(!this.option1Value || !this.option2Value){//请选择币种
                this.$public.msg(this.$t('center.assetInformation[42]'),'warning',this)
                return
            }
            if(!this.$public.Ints(Number(this.changeNum1)) || !this.$public.Ints(Number(this.changeNum2) )){//兑换数量填写有误！
                this.$public.msg(this.$t('center.assetInformation[43]'),'warning',this)
                return
            }

            this.changeCoin1 = this.findLableByValue1(this.option1Value)
            this.changeCoin2 = this.findLableByValue2(this.option2Value)
            this.dialogVisible = true
        }
    }
}
</script>

<style lang=less>
  @import '../../../assets/index.less';
  @import './assetInformation.less';
</style>


